//import axios from "axios";

export default {
    state: {
        info: {}
    },
    mutations: {
        setInfo(state, info) {
            state.info = info
        },
        clearInfo(state) {
            state.info = {}
        }
    },
    actions: {
        //async fetchInfo({dispatch, commit}) {
        async fetchInfo({commit}) {    
            try {
                //const pk = dispatch('getUid')
                //const resp = await axios.post('/api/user/info', {pk})
                const resp = {}
                resp.data = JSON.parse('{"surname":"Матроскин","user_name":"Котофей","patronymic":"Котофеевич"}')
                commit('setInfo', resp.data)
            } catch (e) {
                const eDescription = 'Ошибка запроса данных пользователя!'
                commit('setError', eDescription)
                throw eDescription
            }
        }
    },
    getters: {
        info: s => s.info
    }
}
